import React, {useState } from "react";

const Writenote=()=>
    {
     const [color, setcolor] = useState('dark');
     const change =()=>{
         if(color==='dark'){
             setcolor('success');
         }else{
             setcolor('dark');
         }
     }
        return(
            <>
         <div className="jumbotron box">
    <input type="text" className="form-control inp"  placeholder="Enter Title"/><br/>
    <div className="form-floating">
  <textarea className="form-control" placeholder="Write your note..." id="floatingTextarea"></textarea>
</div>
<br/> 
     <button type="submit" className={`btn btn-${color} pad`} onClick={change} >Add</button>
       </div>
            </>
        )
    }

export default Writenote;